import React from 'react'
import {useState} from 'react'
import './style.css'




export default function FAQ() {

    const [faqSelected, setFaqSelected] = useState(0)
const selectQuestion = (index) => {
    if(index !== faqSelected){
        
        setFaqSelected(index)
    }else{
        setFaqSelected(0)
    }
}


    return (
        <React.Fragment>
                    <div className="faq">
                           <div className="faqItem" style={faqSelected === 1 ? {maxHeight: "200px"} : {maxHeight: "60px"}} onClick={() => selectQuestion(1)}>
                           <div className="faqQ">
                            What is AROMA (ERC20)? <div className="faqQButton">{faqSelected === 1 ? "-" : "+"}</div>
                            </div>
                            <div className="faqA">
                            AROMA is CryptoChefs' in-kitchen currency that is required for all kinds of transactions such as: Buying an NFT, Creating a Recipe, Staking, Marketplace transactions, and so on.
                            </div>
                           </div>
                          
                           <div className="faqItem" style={faqSelected === 2 ? {maxHeight: "220px"} : {maxHeight: "60px"}} onClick={() => selectQuestion(2)}>
                            <div className="faqQ">
                            What is a CryptoChef NFT?  <div className="faqQButton">{faqSelected === 2 ? "-" : "+"}</div>
                            </div>
                            <div className="faqA" >
                            CryptoChef is the NFT that is issued on the Polygon Blockchain. CryptoChefs are unique tokens that represent the Chef and serve as a basis to participate in our CryptoKitchen game.
                               </div>
                           </div>

                           <div className="faqItem" style={faqSelected === 3 ? {maxHeight: "270px"} : {maxHeight: "60px"}} onClick={() => selectQuestion(3)}>
                            <div className="faqQ">
                            What is a Recipe? <div className="faqQButton">{faqSelected === 3 ? "-" : "+"}</div>
                            </div>
                            <div className="faqA">
                            Recipes represent a Crypto Index Fund that a Chef believes will grow in value. It consists of a minimum of 3 and a maximum of 20 Cryptocurrencies (5% steps). Creating a recipe requires to own a CryptoChef and to lock-up AROMA tokens.
                            </div>
                          </div>

                        

                          <div className="faqItem" style={faqSelected === 4 ? {maxHeight: "200px"} : {maxHeight: "60px"}} onClick={() => selectQuestion(4)}>
                            <div className="faqQ"> 
                            What is the Buffet?  <div className="faqQButton">{faqSelected === 4 ? "-" : "+"}</div>
                            </div>
                            <div className="faqA" >
                            As the name suggests, the buffet is where all the active recipes and their details are displayed. It also contains historical performance data of the recipe and the accumulated rewards.
                            </div>
                            </div>

                            <div className="faqItem" style={faqSelected === 5 ? {maxHeight: "355px"} : {maxHeight: "60px"}} onClick={() => selectQuestion(5)}>
                            <div className="faqQ">
                             Why do I have to lock-up AROMA?  <div className="faqQButton">{faqSelected === 5 ? "-" : "+"}</div>
                            </div>
                            <div className="faqA">
                            We want to promote Chefs that put the time and effort into creating amazing recipes in which they really believe in. To prove your commitment, every Chef has to lock-up  at least 200 AROMA tokens. The amount of locked-up tokens also influences your share of the reward pool - the more tokens you lock-up, the higher your share gets!
                            </div>
                            </div>

                        </div>
                 
        </React.Fragment>
    )
}
